<template>
    <div style="height:100%">
      <a-tabs
      hideAdd
      :activeKey="activeKey"
      type="editable-card"
      @edit="onEdit"
      style="padding: 10px;height:100%;"
      class="tabtilt"
       @change="routerchange"
    >
      <a-tab-pane v-for="(pane,i) in panes" :tab="pane.title" :key="i" :closable="!pane.close">
         <Dynamic v-if="pane.type==2" ref="dynamic" :wfeId="pane.id"></Dynamic>
        <list v-else></list>
      </a-tab-pane>
      
    </a-tabs>

    <!-- <router-view :key="key"></router-view> -->
    </div>
</template>

<script>
import {mapState} from 'vuex'
import List from './Process'
import Dynamic from './Dynamics'
export default {
  components:{
      List,Dynamic
  },
    data() {
    return {
      key:''
    };
  },
  computed:{
    ...mapState(["activeKey","panes"])
  },
  methods:{
   
    onEdit(targetKey, action) {
      console.log(arguments);
      if(action=='remove')
      this[action](targetKey);
    },
    remove(targetKey) {
   
      this.$store.dispatch('processremove', targetKey);
    },
     routerchange(activeKey) {
      console.log(activeKey, "activeKey");
       this.$store.dispatch('storerouterchange', activeKey);
    //   if (activeKey == -1) {
    //     this.$router.push({
    //       path: "/tabs"
    //     });
    //   }
     
    }
  },
  watch: {
    '$route' (to, from) { //监听路由是否变化
    //  console.log(from.query.wftId);
		  if(to.query!= from.query ){
        // console.log(to.query.wftId);
        this.key = new Date().getTime()
      }
    }},
}
</script>


<style lang="less" scoped>

</style>
